<template>
  <div class="share-page">
    <div v-if="scan < 2">
      <h3>
        请在24小时内完成新手任务：还有 {{ hour }} 时 {{ min }} 分 {{ sec }} 秒
        到期
      </h3>
      <p>规则：长按二维码2秒->分享到微信群</p>
      <p>只要两名用户通过您分享的二维码打开平台即可完成新手任务！</p>
      <p>状态: {{ scan }} 人已扫描您分享的二维码进入</p>
      <canvas v-if="!downloadImg" id="canvas"></canvas>
      <img v-else :src="downloadImg" alt="加载中..." />
    </div>
    <h3 v-else>您已完成分享任务！点击下面链接和密码获取2022前端开发高级教程</h3>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, ref, onBeforeUnmount } from "vue";
import { useRoute } from "vue-router";
import { v4 as uuidv4 } from "uuid";
import QRCode from "qrcode";
import {
  addFrontEndShareId,
  getFrontEndShareIdInfo,
} from "../api/wxProject.js";
import { staticURL, baseURL } from "../api/config.js";
export default {
  name: "index",
  components: {},
  setup() {
    let route = useRoute();
    let image1 = ref(null);
    let query = route.query;
    let timeId = null;
    const state = reactive({
      info: null,
      hour: 0,
      min: 0,
      sec: 0,
      scan: 0,
      downloadImg: "",
    });

    onMounted(() => {
      init();
    });
    onBeforeUnmount(() => {
      window.clearInterval(timeId);
    });

    const computeTime = (endTime) => {
      let nowTime = new Date().getTime();
      let timeInSec = (endTime - nowTime) / 1000;
      state.hour = parseInt(timeInSec / 60 / 60);
      state.min = parseInt((timeInSec / 60) % 60);
      state.sec = parseInt(timeInSec % 60);
      console.log(state.hour, state.min, state.sec);
    };
    const init = async () => {
      // With promises
      window.document.title = "以为您生成分享二维码";
      let shareId = localStorage.getItem("shareId");
      let res;
      if (!shareId) {
        shareId = uuidv4();
        res = await methods.addFrontEndShareId(shareId);
      } else {
        res = await methods.getFrontEndShareIdInfo(shareId);

        if (!res.shareId) {
          localStorage.removeItem("shareId");
          init();
          return;
        }
      }
      let info = res;
      state.scan = info.scan;
      if (state.scan >= 2) {
        return;
      }
      let endTime = info.startTime + 1000 * 60 * 60 * 24;
      if (endTime <= new Date().getTime()) {
        localStorage.removeItem("shareId");
        init();
        return;
      }
      computeTime(endTime);
      timeId = window.setInterval(() => {
        computeTime(endTime);
      }, 1000);
      QRCode.toDataURL(
        staticURL + "/wxproject1/#/frontEndHelpPage?helpId=" + shareId
      )
        .then((url) => {
          console.log(url);
          const canvas = document.getElementById("canvas");
          const ctx = canvas.getContext("2d");
          const image = new Image();
          image.setAttribute("crossOrigin", "anonymous");
          image.onload = function (event) {
            // console.log(event, this);

            canvas.width = 300;
            canvas.height = 400;

            ctx.drawImage(
              image,
              0,
              0,
              image.width,
              image.height,
              0,
              0,
              300,
              400
            );
            var image1 = new Image();
            image1.onload = function (e) {
              // console.log(event, this);

              ctx.drawImage(
                image1,
                0,
                0,
                image1.width,
                image1.height,
                10,
                85,
                270,
                270
              );
              state.downloadImg = canvas.toDataURL("image/png");
            };
            image1.src = url;
          };
          image.src = staticURL + "/system/templete.jpg";
        })
        .catch((err) => {
          console.error(err);
        });
    };
    const methods = {
      addFrontEndShareId(shareId) {
        return new Promise(
          (resolve) => {
            var formData = new FormData();
            formData.append("shareId", shareId);
            addFrontEndShareId(
              formData,
              (res) => {},
              (res) => {
                localStorage.setItem("shareId", shareId);
                resolve(res);
              }
            );
          },
          (reject) => {}
        );
      },
      getFrontEndShareIdInfo(shareId) {
        return new Promise(
          (resolve) => {
            getFrontEndShareIdInfo(
              {},
              (res) => {
                // console.log(res);
                throw new Error(res.toString());
              },
              (res) => {
                resolve(res);
              },
              {
                method: "get",
                params: {
                  shareId,
                },
              }
            );
          },
          (reject) => {
            reject();
          }
        );
      },
    };

    return {
      ...toRefs(state),
      ...methods,
    };
  },
};
</script>
<style>
#canvas {
  /* width: 15rem; */
}
</style>
